/* eslint-disable no-console */
class CaseCard {
  constructor(rootNode) {
    console.log(rootNode);
    this.rootNode = rootNode;
    this.init();
  }

  init() {
    this.button = this.rootNode.querySelector('.js-case-card__button');
    this.modal = this.rootNode.querySelector('.js-case-card__modal');
    // eslint-disable-next-line no-undef
    document.body.append(this.modal);
    this.bindEventListeners();
  }

  bindEventListeners() {
    this.handleOpenModal = this.handleOpenModal.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);

    this.button.addEventListener('click', this.handleOpenModal);
    this.modal.addEventListener('click', this.handleCloseModal);
  }

  handleOpenModal() {
    // eslint-disable-next-line no-undef
    this.modal.classList.add('case-card__modal--opened');
  }

  handleCloseModal() {
    // eslint-disable-next-line no-undef
    this.modal.classList.remove('case-card__modal--opened');
  }
}

// eslint-disable-next-line no-undef
document.querySelectorAll('.js-case-card').forEach((item) => new CaseCard(item));
